.expenseItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  padding: 0.5rem;
  margin: 1rem 0;
  border-radius: 12px;
  background-color: #4b4b4b;
}

.expenseItemDescription {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: flex-end;
  flex-flow: column-reverse;
  justify-content: flex-start;
}

.expenseItem h2 {
  color: #3a3a3a;
  font-size: 1rem;
  flex: 1;
  margin: 0 1rem;
  color: white;
}

.expenseItemPrice {
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background-color: #40005d;
  border: 1px solid white;
  padding: 0.5rem;
  border-radius: 12px;
}

@media (min-width: 580px) {
  .expenseItem{
    gap: 1rem;
    justify-content: unset;
  }
  .expenseItemDescription {
    width: 100%;
    gap: 1rem;
    flex-flow: unset;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  .expenseItemDescription h2 {
    font-size: 1.25rem;
    flex: unset;
  }

  .expenseItemPrice {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
  }
}
